 + $vendor + \"-\" + $value};\r\n    }\r\n  }\r\n  #{$property}: #{$value};\r\n}\r\n\r\n@mixin fluid-aspect($ratio: 1 1, $selector: \"> :first-child\") {\r\n  $selector: unquote($selector);\r\n\r\n  padding-bottom: percentage(nth($ratio, 2) / nth($ratio, 1));\r\n  position: relative;\r\n\r\n  #{$selector} {\r\n    left: 0;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    width: 100%;\r\n  }\r\n}\r\n","//STYLE GUIDE --styles exclusive to the styleguide page\r\n\r\nh2.title-sg{\r\n  border-bottom: 1px solid $text;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.section-sg{\r\n  padding: em(20) 5%;\r\n  @include container(100%);\r\n}\r\n\r\n.sg-colors{\r\n  list-style-type:none;\r\n  @include container(100%);\r\n\r\n  li{\r\n    font-weight: 700;\r\n    padding: 5px;\r\n    margin: 5px 0;\r\n    text-transform: uppercase;\r\n\r\n    @each $current-color in $sg-colorlist {\r\n\r\n    $i: index($sg-colorlist, $current-color);\r\n\r\n    \r\n\r\n      &:nth-child(#{$i}){\r\n        background: $current-color;\r\n        figcaption{\r\n          color: white;\r\n        }\r\n      }\r\n    }\r\n\r\n    figure{\r\n      width: 100%;\r\n      height: 50px;\r\n      margin:0;\r\n    }\r\n\r\n    figcaption{\r\n      text-align:center;\r\n    }\r\n  }\r\n}\r\n\r\n\r\n","// rem Support\r\n// ===========\r\n\r\n// rem\r\n// ---\r\n// Check for an existing support mixin, or output directly.\r\n// - $prop  : <css property>\r\n// - $val   : <css value>\r\n@mixin susy-rem(\r\n  $prop,\r\n  $val\r\n) {\r\n  $_reqs: (\r\n    variable: rhythm-unit rem-with-px-fallback,\r\n    mixin: rem,\r\n  );\r\n  @if susy-support(rem, $_reqs, $warn: false) and $rhythm-unit == rem {\r\n    @include rem($prop, $val);\r\n  } @else {\r\n    #{$prop}: $val;\r\n  }\r\n}\r\n","// Shared Container API\r\n// ====================\r\n\r\n// Container Output\r\n// ----------------\r\n// - [$width]         : <length>\r\n// - [$justify]       : left | center | right\r\n// - [$math]          : fluid |